  -webkit-text-fill-color: #000000;
}

input,
textarea {
  border-radius: 0;
}
input::placeholder,
textarea::placeholder {
  opacity: 1;
}
input:invalid,
textarea:invalid {
  box-shadow: none;
}

select {
  border-radius: 0;
}

input::-ms-clear, input::-ms-reveal {
  display: none;
}

input[type=search]::-webkit-search-decoration,
input[type=search]::-webkit-search-cancel-button,
input[type=search]::-webkit-search-results-button,
input[type=search]::-webkit-search-results-decoration {
  appearance: none;
}

input::-webkit-outer-spin-button,
input::-webkit-inner-spin-button {
  margin: 0;
  appearance: none;
}

input[type=number] {
  appearance: textfield;
}

select,
textarea,
input:matches([type=email],
[type=number],
[type=password],
[type=search],
[type=tel],
[type=text],
[type=url]) {
  appearance: none;
}

button,
[type=button],
[type=reset],
[type=submit] {
  appearance: none;
}

.wrapper {
  position: relative;
  display: flex;
  flex-direction: column;
  min-height: 100vh;
  /* stylelint-disable-next-line */
  min-height: calc(100 * var(--vh, 1vh));
}
.wrapper .header,
.wrapper .footer {
  flex-shrink: 0;
}
.wrapper main {
  flex-grow: 1;
}

.visually-hidden {
  position: absolute;
  width: 1px;
  height: 1px;
  margin: -1px;
  padding: 0;
  overflow: hidden;
  white-space: nowrap;
  border: 0;
  clip: rect(0 0 0 0);
  clip-path: inset(100%);
}

.scroll-lock-ios {
  position: fixed;
  overflow: hidden;
}

.scroll-lock {
  overflow: hidden;
}

.no-scrollbar {
  -ms-overflow-style: none;
  scrollbar-width: none;
}
.no-scrollbar::-webkit-scrollbar {
  display: none;
}

.js-focus-visible :focus:not(.focus-visible) {
  outline: none;
}

.list-reset {
  margin: 0;
  padding: 0;
  list-style: none;
}

.oll-links {
  cursor: pointer;
}
.oll-links:hover, .oll-links:focus {
  opacity: 0.8;
}
.oll-links:active {
  opacity: 0.3;
}

.container {
  width: 100%;
  min-width: 320px;
  max-width: 1270px;
  margin: 0 auto;
  padding-right: 25px;
  padding-left: 25px;
}
@media (max-width: 1023px) {
  .container {
    max-width: 1023px;
    padding-right: 50px;
    padding-left: 50px;
  }
}
@media (max-width: 767px) {
  .container {
    max-width: 767px;
    padding-right: 25px;
    padding-left: 25px;
  }
}

.btn {
  display: inline-flex;
  margin: 10px 0;
  padding: 10px 20px;
  color: #ffffff;
  text-transform: uppercase;
  background-color: #2c39f2;
  border: none;
  cursor: pointer;
  transition: opacity 0.3s ease;
}
.btn--red {
  background-color: #ff1553;
}
.btn:hover, .btn:focus {
  opacity: 0.8;
}
.btn:active {
  opacity: 0.3;
}
.btn:disabled {
  opacity: 0.5;
  pointer-events: none;
}

.modal {
  position: fixed;
  top: 0;
  right: 0;
  bottom: 0;
  left: 0;
  z-index: 1000;
  display: flex;
  flex-direction: column;
  overflow-y: auto;
  -webkit-overflow-scrolling: touch;
  background-color: rgba(0, 0, 0, 0.8);
  transform: scale(1.2);
  visibility: hidden;
  opacity: 0;
  pointer-events: none;
  -ms-overflow-style: none;
  scrollbar-width: none;
}
.modal::-webkit-scrollbar {
  display: none;
}
.modal__container {
  position: relative;
  display: flex;
  flex-shrink: 0;
  justify-content: center;
  align-items: center;
  width: 100%;
  min-height: 100%;
}
.modal__overlay {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  cursor: pointer;
}
.modal__content {
  position: relative;
  width: 500px;
  margin: 0 auto;
  margin-top: -45px;
  padding: 40px;
  padding-top: 36px;
  padding-bottom: 15px;
  background-color: #00b2ff;
  border-radius: 3px;
  transform: translateX(-8px);
}
.modal.is-active {
  transform: scale(1);
  visibility: visible;
  opacity: 1;
  pointer-events: auto;
}

.modal__heading-wrapper {
  font-size: 16px;
  line-height: 21.5px;
  font-family: "PT Mono", "Arial", sans-serif;
  color: #ffffff;
}
.modal__heading-wrapper h2 {
  margin: 0;
  margin-bottom: 15px;
  font-size: 30px;
  line-height: 42px;
}
.modal__heading-wrapper p {
  margin: 0;
  margin-bottom: 40px;
  transform: translateY(-10px);
}

.modal__input-wrapper {
  display: grid;
  grid-template-areas: "username" "userphone" "question" "button" "agreement";
}

.modal__input input:not([type=checkbox]), .modal__input textarea {
  width: 100%;
  font-family: "PT Mono", "Arial", sans-serif;
  color: #f2f5f6;
  background-color: #019bdf;
  border: none;
  border-radius: 3px;
}
.modal__input input:not([type=checkbox])::placeholder, .modal__input textarea::placeholder {
  color: #ffffff;
}
.modal__input input:not([type=checkbox]):hover, .modal__input input:not([type=checkbox]):focus, .modal__input textarea:hover, .modal__input textarea:focus {
  background-color: #026592;
  outline: none;
  box-shadow: 0 0 0 2px #339a9a;
}
.modal__input input:not([type=checkbox]):active, .modal__input textarea:active {
  outline: none;
  opacity: 0.5;
}
.modal__input input:not([type=checkbox]) {
  padding: 15px;
}
.modal__input textarea {
  width: 100%;
  padding: 25px 15px 25px 15px;
}
.modal__input--username {
  width: 100%;
  margin-bottom: 20px;
  transform: translateY(-11px);
  grid-area: username;
}
.modal__input--userphone {
  max-width: 100%;
  margin-bottom: 20px;
  transform: translateY(-9px);
  grid-area: userphone;
}
.modal__input--question {
  margin-bottom: 25px;
  transform: translateY(-9px);
  grid-area: question;
}
.modal__input--button {
  margin-bottom: 20px;
  padding: 13px;
  padding-bottom: 11px;
  font-weight: 700;
  line-height: 19px;
  color: #ffffff;
  text-transform: uppercase;
  letter-spacing: 0.05em;
  background-color: #041827;
  border: none;
  border-radius: 3px;
  transform: translateY(-17px);
  cursor: pointer;
  grid-area: button;
}
.modal__input--button:hover, .modal__input--button:focus {
  color: #192024;
  background-color: #ffffff;
  opacity: 0.8;
}
.modal__input--button:active {
  opacity: 1;
}
.modal__input--agreement {
  padding-left: 7px;
  font-size: 14px;
  line-height: 16px;
  font-family: "PT Mono", "Arial", sans-serif;
  color: #ffffff;
  transform: translateY(-30px);
  opacity: 0.8;
  grid-area: agreement;
}
.modal__input--agreement input {
  position: relative;
  display: block;
  width: 15px;
  height: 15px;
  margin-right: 10px;
  border: 1px solid #ffffff;
  border-radius: 2px;
  cursor: pointer;
  appearance: none;
}
.modal__input--agreement input:checked::after {
  content: "";
}
.modal__input--agreement input::after {
  position: absolute;
  top: 13%;
  left: 12%;
  display: block;
  width: 7px;
  height: 5px;
  border: 1px solid #ffffff;
  border-top: none;
  border-right: none;
  transform: rotate(-45deg);
}
.modal__input--agreement label {
  display: flex;
  align-items: center;
  padding-top: 13px;
  cursor: pointer;
  user-select: none;
}
.modal__input--agreement:hover, .modal__input--agreement:focus {
  color: rgba(4, 24, 39, 0.5);
}
.modal__input--agreement:active {
  opacity: 1;
}

.modal__close-button {
  position: absolute;
  top: 15px;
  right: 16px;
  z-index: 1;
  width: 40px;
  height: 40px;
  padding: 0;
  color: #ffffff;
  background: #00b2ff;
  border: none;
  cursor: pointer;
}
.modal__close-button:focus::after, .modal__close-button:focus::before, .modal__close-button:hover::after, .modal__close-button:hover::before {
  color: #192024;
}
.modal__close-button:active::after, .modal__close-button:active::before {
  color: #3c3639;
}
.modal__close-button::after, .modal__close-button::before {
  content: "";
  position: absolute;
  top: 47%;
  left: 17%;
  display: block;
  width: 28px;
  height: 1px;
  transform: rotate(45deg);
}
.modal__close-button::before {
  transform: rotate(-45deg);
}

