@media (max-width: 767px) {
  .form__input--button {
    margin-right: 0;
    margin-bottom: 25px;
  }
}
.form__input--button:hover, .form__input--button:focus {
  color: #192024;
  background-color: #ffffff;
  opacity: 0.8;
}
.form__input--button:active {
  opacity: 1;
}
.form__input--agreement {
  padding-left: 12px;
  font-size: 14px;
  line-height: 16px;
  color: #ffffff;
  opacity: 0.8;
  grid-area: agreement;
}
@media (max-width: 1023px) {
  .form__input--agreement {
    padding-left: 0;
  }
}
.form__input--agreement input {
  position: relative;
  display: block;
  width: 21px;
  height: 15px;
  margin-right: 10px;
  border: 1px solid #ffffff;
  border-radius: 2px;
  cursor: pointer;
  appearance: none;
}
