.form__input--agreement input::after {
  position: absolute;
  top: 14%;
  left: 20%;
  display: block;
  width: 7px;
  height: 5px;
  border: 1px solid #ffffff;
  border-top: none;
  border-right: none;
  transform: rotate(-45deg);
